* {
  box-sizing: border-box;
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
}

p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
}

.menu {
  display: flex;
}

.menu__list {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu__item {
  margin-right: 20px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition-property: all;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.menu__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.product-card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__image {
  width: 160px;
  height: 134px;
}

.product-card__link {
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
  display: flex;
}

.product-card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
  transition-property: all;
  transition-duration: .3s;
  display: block;
}

.product-card__name:hover {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__numbers {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button-buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-property: all;
  transition-duration: .3s;
  display: flex;
}

.product-card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-of-type {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.product-cards-container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .product-cards-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .product-cards-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .product-cards-container {
    grid-template-columns: repeat(4, 200px);
  }
}

